.zantimes-logos {
  padding-top: 80px;
}
.zantimes-logos__tagline {
  display: block;
  font-weight: 700;
  color: #1D0056;
  text-transform: uppercase;
  text-align: center;
}
.zantimes-logos__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 2.5rem;
  margin-top: 2rem;
}
.zantimes-logos img {
  margin: 0 auto;
  width: auto;
  height: 60px;
  object-fit: contain;
}
.zantimes-logos .slick-slider {
  margin-top: 3rem;
}
.zantimes-logos .slick-track {
  display: flex;
  align-items: center;
  gap: 4.5em;
  margin-right: -25px;
  margin-left: -25px;
}
.zantimes-logos .slick-list {
  overflow: visible;
}